import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Navigation } from "swiper/modules";
import s from "./Actors.module.scss";
import classNames from "classnames";

const Actors = ({ title, movie }) => {
  return (
    <section className={s.actors}>
      <h2 className={classNames(s.actors__title, "title")}>{title}</h2>
      <Swiper
        breakpoints={{
          500: {
            slidesPerView: 6.5,
          },
          800: {
            slidesPerView: 8.5,
          },
        }}
        modules={[Navigation]}
        spaceBetween={5}
        navigation
        slidesPerView={3.5}
        className={s.slider}>
        {movie.persons &&
          movie.persons.map((el, index) => (
            <SwiperSlide className={s.slide} key={index}>
              <article className={s.actors__card}>
                <div className={s.actors__img}>
                  <img src={el.photo} alt={el.name} />
                </div>
                <div className={s.actors__name}>{el.name === null ? el.enName : el.name}</div>
              </article>
            </SwiperSlide>
          ))}
      </Swiper>
    </section>
  );
};

export default Actors;
